<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process152"></a>

<p><br></p>
<p><span class="rvts17">主线：源力觉醒（</span><span class="rvts50">Powerful Awaking</span><span class="rvts17">）</span><span class="rvts50">.</span><span class="rvts17">下篇</span></p>
<p class="rvps2"><span class="rvts41">任务坐标：</span>
<br><span class="rvts20">X:441,Y:151</span><span class="rvts21">，</span><span class="rvts20">NPC</span><span class="rvts21">：梅斯特湿婆（</span><span class="rvts20">Siva, The Meistr of Seekers</span><span class="rvts21">）</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">增强源力最大值，并找到黑根植物，进行第二次仪式</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><span class="rvts21">在完成了梅斯特湿婆的第一次仪式后，我们获得了</span><span class="rvts20"></span><span class="rvts21">源力视野</span><span class="rvts20"></span><span class="rvts21">技能，接下来梅斯特湿婆会告诉我们首先要增强自己的源力，其次是要找到仪式所需的物品</span><span class="rvts20"></span><span class="rvts21">黑根植物</span><span class="rvts20"></span><span class="rvts21">。完成这两个任务后，便可以进行第二次仪式，获得其它源力技能。</span>
<br><span class="rvts21">增强自己的源力的方法需要寻找死神海岸的一些强大的源力掌控大师教我们。但是，大多数源力掌控大师都不会白交我们的，他们一般需要我们去帮他们完成一些任务才行。</span>
<br><span class="rvts21">具体可以寻求源力掌控大师和相关的支线任务如下：</span>
<br><span class="rvts20">1. </span><span class="rvts21">矮人莫德斯，位于失事洞穴（</span><span class="rvts20">X:73,Y:107</span><span class="rvts21">），</span><span class="rvts20"></span><span class="rvts21">浮木阴影</span><span class="rvts20"></span><span class="rvts21">支线。打败莫德斯并放了他，作为交换，他会让你吃下一颗虚空醒者的心，来增强源力。</span>
<br><span class="rvts20">2. </span><span class="rvts21">汉南格，位于回廊树林（</span><span class="rvts20">X:214,Y:276</span><span class="rvts21">），</span><span class="rvts20"></span><span class="rvts21">机会之窗</span><span class="rvts20"></span><span class="rvts21">支线。帮助汉南格拯救她的徒弟韦典，作为回报，她会教你增强源力。然而汉南格教会你掌控源力，是要以牺牲森林的小动物为代价，并且会使角色暂时失去</span><span class="rvts20"></span><span class="rvts21">动物伙伴</span><span class="rvts20"></span><span class="rvts21">天赋。</span>
<br><span class="rvts20">3. </span><span class="rvts21">在锯木厂，杀掉孤狼首领罗斯特（</span><span class="rvts20">X:140,Y:745</span><span class="rvts21">），并成功帮助萨黑拉逃到精灵营地。作为回报，她会教你增强源力。详见</span><span class="rvts20"></span><span class="rvts21">精灵先知</span><span class="rvts20"></span><span class="rvts21">支线。</span>
<br><span class="rvts20">4. </span><span class="rvts21">石墓花园的瑞克（</span><span class="rvts20">X:515,Y:178</span><span class="rvts21">），是孤狼刺客。帮助他找到黑坑洞中的一块上古石板，作为交换，他也会教你增强源力。详见</span><span class="rvts20"></span><span class="rvts21">慷慨的出价</span><span class="rvts20"></span><span class="rvts21">支线。</span>
<br><span class="rvts20">5. </span><span class="rvts21">在被诅咒的天堂高地，找到阿尔米亚（</span><span class="rvts20">X:708,Y:393</span><span class="rvts21">），并帮他杀掉虚空统治者，作为回报，她会通过魅魔的亲吻，教你增强源力。详见</span><span class="rvts20"></span><span class="rvts21">不可能的恋人</span><span class="rvts20"></span><span class="rvts21">支线。</span>
<br><span class="rvts20">6. </span><span class="rvts21">血月岛的恶魔提倡者（</span><span class="rvts20">X:279,Y:353</span><span class="rvts21">），让你帮助他杀掉血月岛先祖之树处的黑环士兵。作为交换，他会教你增强源力。详见</span><span class="rvts20"></span><span class="rvts21">提倡者</span><span class="rvts20"></span><span class="rvts21">支线。</span>
<br><span class="rvts20">7. </span><span class="rvts21">回廊森林海岸处的贾韩（</span><span class="rvts20">X:293,Y:284</span><span class="rvts21">），让你帮助他杀掉血月岛的提倡者，作为回报，他会教你增强源力。详见</span><span class="rvts20"></span><span class="rvts21">邪恶之物的猎人</span><span class="rvts20"></span><span class="rvts21">支线。</span>
<br><span class="rvts27"></span>
<br><span class="rvts20"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1110.jpg" display="block"><span class="rvts39"></span>
<br><span class="rvts19">在第一次仪式后，黑根植物由于被用完，因此需要在野外找到额外的黑根植物。黑根植物的位置比较集中，大多在</span><span class="rvts20"></span><span class="rvts21">商人恩雅</span><span class="rvts20"></span><span class="rvts21">支线中，恩雅所在的废墟附近。（位置</span><span class="rvts20">X:168,Y:234</span><span class="rvts21">）</span>
<br><span class="rvts20"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1111.jpg" display="block"><span class="rvts39"></span>
<br><span class="rvts19">确保此时至少有两点的</span><span class="rvts20"></span><span class="rvts21">源力值</span><span class="rvts20"></span><span class="rvts21">，接着进行第二次仪式。使用和第一次同样的仪式方法（详见</span><span class="rvts20"></span><span class="rvts21">源力觉醒</span><span class="rvts20"></span><span class="rvts21">上篇），先用小刀割血，再往仪式中加入黑根植物，把用于仪式的血碗放入大火处焚烧，吸入绿烟，开始新的仪式。</span>
<br><span class="rvts20"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1112.jpg" display="block"><span class="rvts39"></span>
<br><span class="rvts19">到了回声大厅，遇到了自己种族的神，与它交谈，获得</span><span class="rvts20"></span><span class="rvts21">源力汲取（</span><span class="rvts20">Source Vampirism</span><span class="rvts21">）</span><span class="rvts20"></span><span class="rvts21">技能。该技能将使角色可以从有源力的敌人身上吸取源力（前提是目标魔法护甲值为</span><span class="rvts20">0</span><span class="rvts21">，不然的话会被魔法护甲阻挡）。</span>
<br><span class="rvts21">它还可以使角色从灵魂身上吸取源力值。</span>
<br><span class="rvts21">之后，使用该技能在神身上，回到现实世界。</span>
<br><span class="rvts20"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1113.jpg" display="block"><span class="rvts39"></span>
<br><span class="rvts19">回到现实世界后，湿婆被虚空怪杀掉。在干掉虚空怪后，与她交谈，她会让你找马拉迪。在复仇女神号找到马拉迪，她会要求你把所有的</span><span class="rvts20">NPC</span><span class="rvts21">同伴都召集到船上后，开始下一章</span><span class="rvts20"></span><span class="rvts21">无名岛</span><span class="rvts20"></span><span class="rvts21">的内容。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return { clickcount : 1  };
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    },
    timer(){
      if (this.clickcount < 3) {
        this.popScroll.refresh()
        this.clickcount = this.clickcount + 1
      }
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
    setInterval(this.timer, 2000);
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



